import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PosSalesReturnUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import Button from "../../../../components/uielements/button";
import HistoryPosSalesReturn from "./history";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import Print from "./print";
import actions from "../../../../redux/pos/posSalesReturn/actions";
import posSIAactions from "../../../../redux/pos/posSalesInvoice/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/pos/sales-invoices";
const { cancelPosSalesInvoice } = posSIAactions;

class UpdatePosSalesReturn extends Component {
  state = {
    visibleModalHistory: false
  };
  componentDidMount() {
    const { editPosSalesReturn } = this.props;
    const { id } = this.props.match.params;
    editPosSalesReturn(id);
    // getTransactions(1, null, null);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.posSalesReturn.error) {
      notification("error", this.props.posSalesReturn.error);
      this.props.clearError();
    }
    if (this.props.posSalesReturn.success) {
      notification("success", this.props.posSalesReturn.success);
      this.props.clearSuccess();
    }
    if (this.props.posSalesReturn.warning) {
      notification("warning", this.props.posSalesReturn.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visibleModalHistory } = this.state;
    visibleModalHistory
      ? this.setState({ visibleModalHistory: false })
      : this.setState({ visibleModalHistory: true });
  };

  handleSubmit = value => {
    const { id } = this.props.match.params;
    const { updatePosSalesReturn } = this.props;
    updatePosSalesReturn(id, value);
  };

  handleSubmitCancelSI = value => {
    const { cancelPosSalesReturn } = this.props;
    const { id } = this.props.match.params;

    cancelPosSalesReturn(id, value);
  };

  render() {
    const {
      loading,
      posSalesReturn,
      printData
    } = this.props.posSalesReturn;
    const modified = { ...posSalesReturn, ...printData };

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="posSalesReturns.button.update" />}
          >
            <div
              style={{
                alignItems: "center",
                display: "flex"
              }}
            >
              <Tooltip placement="top" title="Info">
                <Button
                  style={{
                    border: 0,
                    fontSize: "24px",
                    paddingTop: "2px"
                    // color: theme.palette.warning
                  }}
                  icon="issues-close"
                  onClick={() => this.handleModalHistory()}
                  loading={loading}
                >
                  {/* <IntlMessages id="itemRequests.history" /> */}
                </Button>
              </Tooltip>

              <Print
                loading={loading}
                data={modified}
                posSalesReturn={this.props.posSalesReturn}
              />
              <Button
                onClick={() => {
                  removeSaveData("si");
                  this.props.history.replace(redirecTo);
                }}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          <HistoryPosSalesReturn
            visible={this.state.visibleModalHistory}
            handleModalHistory={this.handleModalHistory}
            description={
              posSalesReturn ? posSalesReturn.description : null
            }
          />

          {loading ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <PosSalesReturnUpdateForm
              handleSubmitCancelSI={this.handleSubmitCancelSI}
              submit={this.handleSubmit}
              posSalesReturn={this.props.posSalesReturn}
              cancelPosSalesInvoice={this.props.cancelPosSalesInvoice}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    posSalesReturn: state.posSalesReturn,
    auth: state.auth
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, cancelPosSalesInvoice })
)(UpdatePosSalesReturn);
