import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/pos/posSalesReturn/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import {
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getSalesInvoices } = actions;

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%"
      // fixed: "left"
    },
    {
      title: getLanguage({ id: "code" }),
      dataIndex: "item_code",
      width: "10%",
      // fixed: "left",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "15%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "quantity" }),
      dataIndex: "qty",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "unit" }),
      width: "10%",
      dataIndex: "unit.unit_code",
      align: "center"
    },
    {
      title: getLanguage({ id: "price" }),
      dataIndex: "item_price",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "tax" }),
      dataIndex: "tax.tax_name",
      width: "10%",
      align: "center"
    },
    {
      title: getLanguage({ id: "discount" }),
      dataIndex: "discount",
      width: "10%",
      align: "right",
      render: text => {
        if (text.slice(-1) === "%") {
          return text;
        } else {
          return <NumberFormat value={text} />;
        }
      }
    },
    {
      title: getLanguage({ id: "subTotal" }),
      dataIndex: "sub_total",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "netSalesPrice" }),
      dataIndex: "netto_sales_price",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalTransaction extends Component {
  state = {
    filter: {
      payment_status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    limit: 0,
    search: null
  };

  componentDidUpdate(prevProps) {
    if (
      (this.state.limit === 0 &&
        this.props.posSalesReturn.posSalesReturn) ||
      this.props.posSalesReturn.posSalesReturn !==
        prevProps.posSalesReturn.posSalesReturn
    ) {
      this.handleGetSalesInvoices(null, "");
      if (this.state.limit === 0) {
        this.setState({
          limit: 1
        });
      }
    }
  }

  handleGetSalesInvoices = (search, addFilter) => {
    const { getSalesInvoices } = this.props;
    const { posSalesReturn } = this.props;
    const data = posSalesReturn.posSalesReturn
      ? posSalesReturn.posSalesReturn
      : [];
    let string = "";
    if (data) {
      string =
        "id_sales_return=" +
        data.id_sales_return +
        "&transaction_date=" +
        data.return_date;
      let searchValue = null;
      if (search) {
        searchValue = search;
      }
      if (addFilter) {
        string += "&" + addFilter;
      }
      // console.log("data", data, "string", string);
      getSalesInvoices(1, searchValue, string);
    }
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  // Handle search
  handleSearch = value => {
    this.handleGetSalesInvoices(value, "");
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    this.setState(
      {
        filter: {
          payment_status: null,
          start_date: null,
          end_date: null,
          pagination: null
        },
        search: null
      },
      () => this.handleGetSalesInvoices(null, "")
    );
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    //const { current_page } = this.props.customer;
    const { filter } = this.state;
    let string = "";

    if (filter.payment_status) {
      string += "payment_status=" + filter.payment_status;
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    // console.log(string);
    this.setState({ search: null });
    this.handleGetSalesInvoices(null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    this.setState(
      {
        filter: {
          payment_status: null,
          start_date: null,
          end_date: null,
          pagination: null
        },
        search: null
      },
      () => this.handleGetSalesInvoices(null, "")
    );
  };

  // Handle pagination
  handlePageChange = page => {
    const { getSalesInvoices } = this.props;
    const { search, filter } = this.props.salesInvoice;
    getSalesInvoices(page, search, filter);
  };

  render() {
    // console.log(this.props);
    const { visible, handleModal, posSalesReturn } = this.props;
    const { salesInvoices, locations, customers } = posSalesReturn
      ? posSalesReturn
      : [];
    const { loadingForeign, loadingSalesInvoices } = posSalesReturn
      ? posSalesReturn
      : false;

    const columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "sales_invoice_no",
        rowKey: "sales_invoice_no",
        width: "20%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "pending";
              break;
            case 2:
              className = "processed";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "pending";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_sales_invoice);
                  }}
                  href="javascript:;"
                >
                  <TransNo>{text}</TransNo>
                </a>
              </Tooltip>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "10%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "15%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "15%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "paymentStatus" }),
        dataIndex: "payment_status",
        rowKey: "payment_status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 0:
              className = "new-payment";
              display = getLanguage({ id: "new" });
              break;
            case 1:
              className = "billed-payment";
              display = getLanguage({ id: "billed" });
              break;
            case 2:
              className = "paid-payment";
              display = getLanguage({ id: "paid" });
              break;
            default:
              className = "new-payment";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "processed";
              display = getLanguage({ id: "processed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const dataSource = [];
    Object.keys(salesInvoices).map((item, index) => {
      return dataSource.push({
        ...salesInvoices[item],
        key: item
      });
    });

    const { current_page_si, page_size_si, total_si } = this.props
      .posSalesReturn
      ? this.props.posSalesReturn
      : [];

    return (
      <Modal
        title={<IntlMessages id="salesReturns.importSalesInvoice" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
      >
        <Row style={{ marginBottom: "20px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                `${getLanguage({ id: "transactionNo" })}, ` +
                `${getLanguage({ id: "createdBy" })}`
              }
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleSearch.bind(this, this.state.search)}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "20px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "12px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="customers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_customer"
                      )}
                      value={
                        this.props.id_customer ? this.props.id_customer : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.customer" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      disabled={true}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {customers.map((data, key) => (
                        <Option key={key} value={data.id_customer}>
                          {`${data.customer_code} (${data.customer_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.props.id_location ? this.props.id_location : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      disabled={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "12px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="space-between" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="paymentStatus" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "payment_status"
                          )}
                          value={
                            this.state.filter.payment_status
                              ? this.state.filter.payment_status
                              : []
                          }
                          style={{ width: "90%" }}
                          placeholder={<IntlMessages id="paymentStatus" />}
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={"0"}>
                            {getLanguage({ id: "new" })}
                          </Option>
                          <Option value={1}>
                            {getLanguage({ id: "billed" })}
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <Row type="flex">
                          <Col span={12}>
                            <label>
                              <IntlMessages id="dataPerPage" />
                            </label>
                            <br />
                            <Select
                              showSearch
                              onChange={this.handleFilterChange.bind(
                                this,
                                "pagination"
                              )}
                              value={
                                this.state.filter.pagination
                                  ? this.state.filter.pagination
                                  : page_size_si
                              }
                              style={{ width: "80%" }}
                              // placeholder={
                              //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                              // }
                              optionFilterProp="children"
                              filterOption={true}
                              //allowClear={true}
                              //showArrow={false}
                            >
                              <Option key={1} value={10}>
                                10
                              </Option>
                              <Option key={2} value={25}>
                                25
                              </Option>
                              <Option key={3} value={50}>
                                50
                              </Option>
                              <Option key={4} value={100}>
                                100
                              </Option>
                            </Select>
                          </Col>

                          <Col
                            span={12}
                            style={{
                              marginTop: "auto"
                            }}
                          >
                            <Button
                              size="small"
                              type="primary"
                              style={{ marginRight: "10px" }}
                              onClick={this.handleFilter}
                            >
                              <IntlMessages id="filter.submit" />
                            </Button>
                            <Button
                              onClick={this.handleResetFilter}
                              size="small"
                            >
                              <IntlMessages id="filter.reset" />
                            </Button>
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>
        <Row>
          <Col>
            {dataSource.length === 0 && !loadingSalesInvoices ? (
              <Empty
                description={<IntlMessages id="salesInvoices.table.noData" />}
              />
            ) : (
              <Table
                dataSource={dataSource}
                columns={columns}
                loading={loadingSalesInvoices}
                expandedRowRender={record =>
                  expandedRowRender(record.sales_invoice_details)
                }
                expandIcon={props => (
                  <Button
                    onClick={e => props.onExpand(props.record, e)}
                    size="small"
                  >
                    <IntlMessages id="button.view" />
                  </Button>
                )}
                // Set pagination, jika ada page_size mengunakan pagination API
                // jika tidak ada page_size menggunakan pagination bawaan
                pagination={
                  page_size_si
                    ? {
                        current: current_page_si,
                        pageSize: parseInt(page_size_si),
                        total: total_si,
                        onChange: this.handlePageChange.bind(this)
                      }
                    : true
                }
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(connect(null, { getSalesInvoices }))(ModalTransaction);
