const actions = {
  getPosSalesReturns: (page, search, filter) => ({
    type: "GET_POS_SALES_RETURNS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  synchronizePos: () => ({
    type: "SYNCHRONIZE_POS_SALES_RETURNS_REQUEST"
  }),
  editPosSalesReturn: (id_sales_return, search) => ({
    type: "EDIT_POS_SALES_RETURN_REQUEST",
    payload: { id_sales_return: id_sales_return, search: search }
  }),
  storeItem: (id_sales_return, data) => ({
    type: "STORE_ITEM_POS_SALES_RETURN_REQUEST",
    payload: { id_sales_return, body: data }
  }),
  updateItem: (id_sales_return, data) => ({
    type: "UPDATE_ITEM_POS_SALES_RETURN_REQUEST",
    payload: { id_sales_return, body: data }
  }),
  updatePosSalesReturn: (id_sales_return, data) => ({
    type: "UPDATE_POS_SALES_RETURN_REQUEST",
    payload: {
      id_sales_return: id_sales_return,
      body: data
    }
  }),
  confirmPosSalesReturn: (id_sales_return, data) => ({
    type: "CONFIRM_POS_SALES_RETURN_REQUEST",
    payload: { id_sales_return, body: data }
  }),
  cancelPosSalesReturn: (id_sales_return, data) => ({
    type: "CANCEL_POS_SALES_RETURN_REQUEST",
    payload: { id_sales_return, body: data }
  }),
  uploadDocument: (id_sales_return, data) => ({
    type: "UPLOAD_POS_SALES_RETURN_DOCUMENT_REQUEST",
    payload: {
      id_sales_return,
      body: data
    }
  }),
  downloadDocument: id_pos_sales_return_document => ({
    type: "DOWNLOAD_POS_SALES_RETURN_DOCUMENT_REQUEST",
    payload: { id_pos_sales_return_document }
  }),
  deleteDocument: (id_pos_sales_return_document, id_sales_return) => ({
    type: "DELETE_POS_SALES_RETURN_DOCUMENT_REQUEST",
    payload: {
      id_pos_sales_return_document: id_pos_sales_return_document,
      id_sales_return: id_sales_return
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_POS_SALES_RETURN",
    payload: { body: data }
  }),
  getSalesInvoices: (page, search, filter) => ({
    type: "GET_FILTER_SALES_INVOICES_POS_ST_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  }),
  setTaxBase: tax_bases => ({
    type: "SET_TAX_BASES_POS_SALES_RETURN",
    payload: { tax_bases: tax_bases }
  })
};

export default actions;
