const actions = {
  getPosSalesInvoices: (page, search, filter) => ({
    type: "GET_POS_SALES_INVOICES_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  synchronizePos: () => ({
    type: "SYNCHRONIZE_POS_SALES_INVOICES_REQUEST"
  }),
  editPosSalesInvoice: (id_sales_invoice, search) => ({
    type: "EDIT_POS_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice: id_sales_invoice, search: search }
  }),
  updatePosSalesInvoice: (id_sales_invoice, data) => ({
    type: "UPDATE_POS_SALES_INVOICE_REQUEST",
    payload: {
      id_sales_invoice: id_sales_invoice,
      body: data
    }
  }),
  storeItem: (id_sales_invoice, data) => ({
    type: "STORE_ITEM_POS_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  updateItem: (id_sales_invoice, data) => ({
    type: "UPDATE_ITEM_POS_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  updateItemGrouping: (id_sales_invoice, id_item, data) => ({
    type: "UPDATE_ITEM_GROUPING_POS_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, id_item, body: data }
  }),
  confirmPosSalesInvoice: (id_sales_invoice, data) => ({
    type: "CONFIRM_POS_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  confirmPosSalesInvoices: data => ({
    type: "CONFIRM_POS_SALES_INVOICES_REQUEST",
    payload: { body: data }
  }),
  cancelPosSalesInvoice: (id_sales_invoice, data) => ({
    type: "CANCEL_POS_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  uploadDocument: (id_sales_invoice, data) => ({
    type: "UPLOAD_POS_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: {
      id_sales_invoice,
      body: data
    }
  }),
  downloadDocument: id_pos_sales_invoice_document => ({
    type: "DOWNLOAD_POS_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: { id_pos_sales_invoice_document }
  }),
  deleteDocument: (id_pos_sales_invoice_document, id_sales_invoice) => ({
    type: "DELETE_POS_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: {
      id_pos_sales_invoice_document: id_pos_sales_invoice_document,
      id_sales_invoice: id_sales_invoice
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_POS_SALES_INVOICE",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearWarning: () => ({
    type: "CLEAR_WARNING"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  }),
  setTaxBase: tax_bases => ({
    type: "SET_TAX_BASES_POS_SALES_INVOICE",
    payload: { tax_bases: tax_bases }
  })
};

export default actions;
