import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/pos/posSalesReturn/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Icon,
  Input,
  // InputNumber,
  Modal,
  Popconfirm,
  Row,
  Select,
  Steps,
  Tabs,
  Tooltip
} from "antd";

import EditableTable from "./editableTable";
import ModalCancel from "./modalCancel";
import ModalMultiPayment from "./modalMultiPayment";
import ModalSalesInvoice from "./modalSalesInvoice";
import PosSalesReturnDocUpdateForm from "./updateDocForm";
// import { Object } from "core-js";
import { customDot, StatusTag } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const Step = Steps.Step;
const TabPane = Tabs.TabPane;
const warning = Modal.warning;

const {
  setPrintData,
  confirmPosSalesReturn,
  cancelPosSalesReturn,
  unconfirmPosSalesReturn
} = actions;

class PosSalesReturnUpdateForm extends Component {
  state = {
    activeCustDisc: null,
    clear: false,
    currency_rate: 1,
    customer: {},
    discount_amount: 0,
    err: false,
    errInvAmount: false,
    errPayment: [],
    fullDiscMultiplier: null,
    idCustomer: null,
    idLocation: null,
    id_transaction: null, //simpan id_transaksi dari transaksi SI terkait
    idTransactionExist: false,
    items: [],
    itemDetails: {
      sales_return_details: []
    },
    limit: 0,
    not_exists: 0,
    payments: [],
    paymentDetails: {
      payments: []
    },
    sum_subtotal_discount_item: 0,
    // tab: null,
    tab: "1",
    transaction_no: null, //simpan transaction number dari transaksi SI terkait
    viewed: false,
    visible: false,
    visibleModalCancel: false,
    visibleModalSalesInvoice: false,
    visibleSalesInvoice: false
  };

  componentDidMount = () => {
    /* Init Restore Autosave */
    // let restore = getSaveData("pi");
    // if (restore) {
    //   restore.fields.transaction_date = moment(restore.fields.transaction_date);
    //   restore.fields.payment_due_date = moment(restore.fields.payment_due_date);
    //   this.setState({ restore: restore });
    // }
    /* END Init Restore Autosave */

    // when ordered qty is exceeded
    if (this.props.posSalesReturn.info) {
      const { info } = this.props.posSalesReturn;
      this.setState({ exceededItems: info });
    }
  };

  componentDidUpdate = (prevProps, prevState) => {
    const { itemDetails, not_exists, viewed } = this.state;
    const { warning_message } = this.props.posSalesReturn;
    const { loading, posSalesReturn } = this.props.posSalesReturn;

    if (
      !loading &&
      prevState.itemDetails.sales_return_details.length === 0 &&
      this.state.limit === 0
    ) {
      let payments = [];
      if (posSalesReturn.payments) {
        payments = posSalesReturn.payments.map(element => {
          const {
            payment_categories,
            payment_terms
          } = this.props.posSalesReturn;

          let data = { ...element };

          // cari payment term untuk get payment term code yang sesuai
          if (!data.payment_term_code) {
            const findPayTerm = payment_terms.find(
              data => data.id_payment_term === element.id_payment_term
            );
            if (findPayTerm) {
              data.payment_term_code = findPayTerm.payment_term_code;
            } else data.payment_term_code = "-";
          }

          // return
          return data;
        });
      }
      // sample payments = [
      //   {
      //     payment_category_code: "Cash",
      //     payment_type: 1,
      //     id_payment_term: "100",
      //     payment_term_code: "CASH",
      //     payment_amount: parseFloat(posSalesReturn.total_amount) / 4
      //   },
      //   {
      //     payment_category_code: "Credit",
      //     payment_type: 2,
      //     id_payment_term: "100",
      //     payment_term_code: "CASH",
      //     payment_amount: parseFloat(posSalesReturn.total_amount) / 4
      //   },
      //   {
      //     payment_category_code: "Card",
      //     payment_type: 3,
      //     id_payment_term: null,
      //     payment_amount: parseFloat(posSalesReturn.total_amount) / 4
      //   },
      //   {
      //     payment_category_code: "OVO",
      //     payment_type: 4,
      //     id_payment_term: null,
      //     payment_amount: parseFloat(posSalesReturn.total_amount) / 4
      //   }
      // ];
      this.setState({
        customer: posSalesReturn ? posSalesReturn.customer : null,
        idCustomer: posSalesReturn ? posSalesReturn.id_customer : null,
        idLocation: posSalesReturn ? posSalesReturn.id_location : null,
        id_transaction: posSalesReturn
          ? posSalesReturn.id_transaction
          : null,
        idTransactionExist: posSalesReturn
          ? posSalesReturn.id_transaction
            ? true
            : false
          : false,
        items: posSalesReturn ? posSalesReturn.sales_return_details : [],
        limit: 1,
        payments: payments,
        paymentDetails: {
          payments: posSalesReturn ? posSalesReturn.payments : []
        },
        transaction_no: posSalesReturn
          ? posSalesReturn.transaction_no
          : null
      });
      if (posSalesReturn) {
        this.props.form.setFieldsValue({
          transaction_no: posSalesReturn.transaction_no
        });
      }
    }

    if (not_exists > 0 && !viewed && warning_message && warning_message.title) {
      this.setState({ viewed: true }, () => this.showWarningItem());
    }

    if (
      prevState.itemDetails.sales_return_details !==
      itemDetails.sales_return_details
    ) {
      const { setPrintData } = this.props;
      setPrintData(itemDetails);
    }
  };

  clearItems = (items, total_qty, sum_subtotal_discount_item, not_exists) => {
    this.setState({
      items: [],
      itemDetails: { sales_return_details: items },
      total_qty: total_qty,
      sum_subtotal_discount_item,
      not_exists,
      clear: false
    });
  };

  setErrPayment = errPayment => {
    this.setState({ errPayment });
  };

  handleModalMultiPayment = () => {
    this.setState({
      visibleMultiPayment: !this.state.visibleMultiPayment
    });
  };

  handleModalSalesInvoice = () => {
    this.setState({
      visibleModalSalesInvoice: !this.state.visibleModalSalesInvoice
    });
  };

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    });
  };
  handleSelectModalSalesInvoice = value => {
    const { salesInvoices } = this.props.posSalesReturn;
    let salesInvoice = salesInvoices.find(
      curr_data => curr_data.id_sales_invoice === value
    );
    // console.log("value", value, "salesInvoice", salesInvoice);

    this.setState({
      transaction_no: salesInvoice.sales_invoice_no,
      id_transaction: salesInvoice.id_sales_invoice
    });
    this.props.form.setFieldsValue({
      transaction_no: salesInvoice.sales_invoice_no
    });

    this.handleModalSalesInvoice();
  };

  handleSaveItemList = (
    items,
    total_qty,
    sum_subtotal_discount_item,
    sync = false,
    dataChanged = false
  ) => {
    const setStateData = {
      itemDetails: { sales_return_details: items },
      total_qty: total_qty,
      sum_subtotal_discount_item,
      clear: false
    };
    this.setState(setStateData);
  };

  handleOkMultiPayment = payments => {
    this.setState({
      paymentDetails: payments,
      visibleMultiPayment: false
    });
  };

  showWarningItem = () => {
    const { warning_message } = this.props.posSalesReturn;

    warning({
      title: warning_message.title,
      content: warning_message.content,
      icon: <Icon type="info-circle" />,
      okText: "OK",
      onOk: () => {}
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue, getFieldError } = this.props.form;

    const { posSalesReturn } = this.props;
    const {
      customers,
      locations,
      // cash_banks,
      // base_currency,
      payment_terms
    } = posSalesReturn ? posSalesReturn : [];

    const { user } = this.props;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const {
      loading,
      loadingCancel,
      loadingConfirm,
      loadingForeign,
      loadingItems,
      loadingSalesInvoices,
      loadingTransactions
      // loadingTable
    } = posSalesReturn ? posSalesReturn : [];
    const data = posSalesReturn.posSalesReturn
      ? posSalesReturn.posSalesReturn
      : [];

    const transactionStatus = () => {
      let curr = 0;
      switch (data.status) {
        case 1:
          curr = 0;
          break;
        case 2:
          curr = 1;
          break;
        case 3:
          curr = 2;
          break;
        default:
          curr = 0;
          break;
      }
      return curr;
    };

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical">
        <ModalSalesInvoice
          visible={this.state.visibleModalSalesInvoice}
          posSalesReturn={posSalesReturn}
          handleModal={this.handleModalSalesInvoice}
          handleSelectModal={this.handleSelectModalSalesInvoice}
          id_location={getFieldValue("id_location")}
          id_customer={getFieldValue("id_customer")}
        />
        <ModalMultiPayment
          errPayment={this.state.errPayment}
          handleModal={this.handleModalMultiPayment}
          notExist={this.state.not_exists}
          payments={this.state.payments}
          paymentTerms={payment_terms}
          permission={permissions.includes("Confirm Pos Sales Return")}
          setErrPayment={this.setErrPayment}
          status={data.status}
          submit={this.handleOkMultiPayment}
          totalAmount={data.total_amount}
          visible={this.state.visibleMultiPayment}
        />

        <ModalCancel
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancelSI}
          visible={this.state.visibleModalCancel}
        />

        <Row style={{ margin: "12px 0px" }}>
          <Col span={12}>
            <Steps
              size="small"
              progressDot={customDot}
              current={transactionStatus()}
            >
              <Step title={getLanguage({ id: "pending" })} />
              <Step title={getLanguage({ id: "processed" })} />
              {/* <Step title={getLanguage({ id: "cancelled" })} /> */}
            </Steps>
          </Col>
        </Row>

        <Tabs
          defaultActiveKey="1"
          onTabClick={key => this.setState({ tab: key })}
        >
          <TabPane
            tab={<IntlMessages id="purchaseInvoices.tabs.detail" />}
            key="1"
          >
            <Row>
              <Col span={12} style={{ paddingRight: "15px" }}>
                <InputGroup>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="salesReturns.salesReturnNo" />}
                    >
                      {getFieldDecorator("sales_return_no", {
                        initialValue: data.sales_return_no
                      })(<Input disabled={true} />)}
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      {...formItemLayout}
                      label={
                        <IntlMessages id="purchaseInvoices.transactionDate" />
                      }
                      style={{ marginBottom: 0, paddingBottom: 0 }}
                    >
                      {getFieldDecorator("return_date", {
                        initialValue: moment(
                          data.return_date,
                          "YYYY-MM-DD HH:mm:ss"
                        )
                      })(
                        <DatePicker
                          style={{ width: "100%" }}
                          showTime
                          format="DD MMMM YYYY HH:mm:ss"
                          disabled={true}
                          allowClear={false}
                          disabledDate={e => e.valueOf() > moment()}
                        />
                      )}
                    </Form.Item>
                  </Col>
                </InputGroup>

                <Col span={24}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="posSalesReturns.location" />
                            }
                          >
                            <IntlMessages id="purchaseInvoices.location" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        {getFieldDecorator("id_location", {
                          initialValue: data.id_location,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            disabled={true}
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="purchaseInvoices.location" />
                            }
                            loading={loadingForeign}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {locations && locations.length > 0
                              ? locations.map((data, key) => (
                                  <Option key={key} value={data.id_location}>
                                    {`${data.location_code} (${data.location_name})`}
                                  </Option>
                                ))
                              : null}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="sidebar.customer" />}
                      >
                        {getFieldDecorator("id_customer", {
                          initialValue: data.id_customer,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            disabled={true}
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="salesOrders.filter.select.customer" />
                            }
                            loading={loadingForeign}
                            notFoundContent={
                              <Empty
                                description={<IntlMessages id="noData" />}
                              />
                            }
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {customers && customers.length > 0
                              ? customers.map((data, key) => (
                                  <Option key={key} value={data.id_customer}>
                                    {`${data.customer_code} (${data.customer_name})`}
                                  </Option>
                                ))
                              : null}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
                <Col span={24}>
                  <InputGroup>
                    {/* import SI - START */}
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="salesReturns.importTransaction.help" />
                            }
                          >
                            <IntlMessages id="Sales Invoice" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        <Button
                          icon="select"
                          disabled={
                            loadingSalesInvoices ||
                            this.state.idTransactionExist
                          }
                          onClick={this.handleModalSalesInvoice}
                          style={{ height: "31px", width: "100%" }}
                          // shape="circle"
                        >
                          <IntlMessages id="salesReturns.buttonImportSI" />
                        </Button>
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="salesInvoice.no" />}
                      >
                        {getFieldDecorator("transaction_no", {
                          // initialValue: data.id_return_reason,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(<Input disabled={true} />)}
                      </Form.Item>
                    </Col>
                    {/* import SI - END */}
                  </InputGroup>
                </Col>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Form.Item
                  {...formTextArea}
                  label={<IntlMessages id="purchaseInvoices.note" />}
                >
                  {getFieldDecorator("note", {
                    initialValue: data.note
                  })(<TextArea autosize={{ minRows: 4 }} disabled={true} />)}
                </Form.Item>
              </Col>
            </Row>

            <Divider orientation="left" style={{ fontSize: 14 }}>
              <IntlMessages id="posSalesInvoices.divider.item" />
            </Divider>

            <Row style={{ marginBottom: "40px" }}>
              <Col span={24}>
                <EditableTable
                  clear={this.state.clear}
                  clearItem={this.clearItem}
                  clearItems={this.clearItems}
                  custDisc={this.state.custDisc}
                  handleSaveItemList={this.handleSaveItemList}
                  item={this.state.item}
                  items={this.state.items}
                  // itemsFromForeignKeys={items}
                  loadingItems={loadingItems}
                  loadingTransactions={loadingTransactions}
                  permissions={permissions}
                  status={data.status}
                />
              </Col>
              <Col span={12}>
                <Col span={10} style={{ marginTop: "15px", textAlign: "left" }}>
                  <strong>
                    <IntlMessages id="purchaseInvoices.totalQty" />
                  </strong>
                  <strong>
                    <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
                  </strong>
                </Col>
                <Col span={13} offset={1} style={{ marginTop: "15px" }}>
                  <strong>
                    <NumberFormat value={this.state.total_qty} />
                  </strong>
                </Col>
              </Col>
            </Row>

            <Row>
              <Col span={12}>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "35px" }}
                >
                  <Col span={12} style={{ marginTop: "5px" }}>
                    <label className="">
                      <IntlMessages id="purchaseInvoices.taxInvoiceNo" />
                    </label>
                  </Col>
                  <Col span={11} offset={1}>
                    {getFieldDecorator("tax_invoice_no", {
                      initialValue: data.tax_invoice_no
                    })(
                      <Input
                        disabled={true}
                        placeholder={getLanguage({
                          id: "purchaseInvoices.taxInvoiceNo"
                        })}
                        size="small"
                        style={{
                          marginTop: "3px",
                          borderColor: getFieldError("tax_invoice_no")
                            ? "#f5222d"
                            : "#d9d9d9"
                        }}
                      />
                    )}
                  </Col>
                </Row>
              </Col>
              <Col span={12} style={{ paddingLeft: "15px" }}>
                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.exlDiscountAndTax" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.total_exclude_discount_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={10}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.discount" />{" "}
                    {this.state.activeCustDisc ? (
                      <Tooltip
                        placement="bottom"
                        // title={
                        //   <IntlMessages id="posSalesReturns.getDiscount.help" />
                        // }
                      >
                        <StatusTag
                          className="billed-payment"
                          style={{ marginLeft: "4px" }}
                        >
                          {this.state.activeCustDisc}
                        </StatusTag>
                      </Tooltip>
                    ) : null}
                  </Col>
                  <Col span={5} style={{ paddingLeft: "7px" }}>
                    <Tooltip
                      placement="bottom"
                      title={this.state.fullDiscMultiplier || ""}
                    >
                      {getFieldDecorator("discount", {
                        initialValue: parseFloat(
                          data.discount_amount
                        ).toString()
                      })(
                        <Input
                          size="small"
                          disabled={true}
                          style={{ marginTop: "3px" }}
                        />
                      )}
                    </Tooltip>
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.discount_amount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.totalDiscount" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.total_discount} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="posSalesInvoices.service" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.service_charge} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="purchaseInvoices.totalTax" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.total_tax} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <IntlMessages id="posSalesInvoices.totalRounding" />
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <span>
                      <NumberFormat value={data.total_rounding} />
                    </span>
                  </Col>
                </Row>

                <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                  <Col
                    span={15}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <strong>
                      <IntlMessages id="total" />
                    </strong>
                  </Col>
                  <Col
                    span={8}
                    offset={1}
                    style={{ marginTop: "5px", textAlign: "right" }}
                  >
                    <Button
                      block
                      style={{ textAlign: "right", padding: "0 5px" }}
                      onClick={this.handleModalMultiPayment}
                      type={
                        this.state.errPayment.length > 0 ? "danger" : "default"
                      }
                    >
                      <NumberFormat value={data.total_amount} />
                      <Icon type="edit" />
                    </Button>
                  </Col>
                </Row>
              </Col>
            </Row>
          </TabPane>

          <TabPane
            tab={<IntlMessages id="jobCostings.tab.attachment" />}
            key="2"
          >
            <PosSalesReturnDocUpdateForm
              posSalesReturn={this.props.posSalesReturn}
              params={this.props.params}
              form={this.props.form}
            />
          </TabPane>
        </Tabs>

        {this.state.tab !== "2" ? (
          <Form.Item {...tailFormItemLayout}>
            <div
              style={{
                alignItems: "center",
                display: "flex",
                marginTop: "24px"
              }}
            >
              {/* Confirm */}
              <Popconfirm
                title={<IntlMessages id="confirmTransaction" />}
                onConfirm={() => {
                  this.props.form.validateFieldsAndScroll((err, values) => {
                    const { payments } = this.state.paymentDetails;
                    // validasi payment term
                    let index = [];
                    payments.map((payment, key) => {
                      if (
                        payment.payment_type === 2 &&
                        !payment.id_payment_term
                      ) {
                        return index.push(key);
                      }
                    });
                    if (index.length > 0) {
                      return this.setState({ errPayment: [...index] });
                    } else {
                      this.setState({ errPayment: [] });
                    }

                    let body = Object.assign({}, this.state.paymentDetails, {
                      transaction_no: values.transaction_no,
                      id_transaction: this.state.id_transaction
                    });
                    if (!err) {
                      this.setState({ err: false });
                      // console.log("body", body);
                      // if (this.state.idTransactionExist) {
                      //   return this.props.cancelPosSalesInvoice(
                      //     data.id_sales_return,
                      //     body
                      //   );
                      // } else {
                      return this.props.confirmPosSalesReturn(
                        data.id_sales_return,
                        body
                      );
                      // }
                    } else {
                      return this.setState({ err: true });
                    }
                  });
                }}
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Button
                  style={{
                    marginRight: "5px",
                    padding: "0px 15px",
                    display: "block"
                  }}
                  disabled={
                    data.status > 1 ||
                    this.state.not_exists > 0 ||
                    this.state.errPayment.length > 0 ||
                    !permissions.includes("Confirm Pos Sales Return")
                      ? true
                      : false
                  }
                  loading={loading || loadingConfirm}
                >
                  <IntlMessages id="button.confirmTransaction" />
                </Button>
              </Popconfirm>

              {/* Cancel */}
              <Button
                style={{
                  marginRight: "5px",
                  padding: "0px 15px",
                  display: "block"
                }}
                disabled={
                  data.status !== 2 ||
                  !permissions.includes("Unconfirm Pos Sales Return")
                    ? true
                    : false
                }
                loading={loading || loadingCancel}
                onClick={this.handleModalCancel}
              >
                <IntlMessages id="button.unconfirmTransaction" />
              </Button>
            </div>

            <br />
            {this.state.err ? (
              <span style={{ color: "#f5222d" }}>
                <IntlMessages id="form.error" />
              </span>
            ) : null}

            {this.state.errPayment.length > 0 ? (
              <span style={{ color: "#f5222d" }}>
                <IntlMessages id="errorPaymentTerm" />
              </span>
            ) : null}
          </Form.Item>
        ) : null}
      </Form>
    );
  }
}

const WrappedSalesReturnUpdateForm = Form.create({
  name: "edit_pos_sales_return"
})(PosSalesReturnUpdateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(mapStateToProps, {
    setPrintData,
    confirmPosSalesReturn,
    cancelPosSalesReturn,
    unconfirmPosSalesReturn
  })
)(WrappedSalesReturnUpdateForm);
